import {
  isXodBaseMessageType,
  KafkaCallbackResponse,
  XodBaseMessageType,
} from '@xip-online-data/types';
import { KafkaJS } from '@confluentinc/kafka-javascript';
import { Logger } from '@transai/logger';
import { AbstractRdKafkaService } from './abstract-rdkafka-service';
import { KafkaCallbackWrapper } from './types';

export class RdKafkaBaseService extends AbstractRdKafkaService {
  private readonly callbackWrappers = new Map<
    string,
    Array<KafkaCallbackWrapper>
  >();

  protected initialized = false;

  public setCallbackFunction(
    callbackFunction: (
      message: XodBaseMessageType,
    ) => Promise<KafkaCallbackResponse>,
    eventType: string = RdKafkaBaseService.DEFAULT_CALLBACK_EVENT_TYPE,
    identifier = '',
  ): void {
    if (!this.callbackWrappers.has(eventType)) {
      this.callbackWrappers.set(eventType, []);
    }

    let callback = callbackFunction;

    if (this.messageMonitor !== undefined) {
      callback = this.messageMonitor.processMessage(callback);
    }

    this.callbackWrappers.get(eventType)?.push({
      callback,
      identifier:
        eventType === RdKafkaBaseService.DEFAULT_CALLBACK_EVENT_TYPE
          ? this.baseYamlConfig.processIdentifier
          : identifier,
    });
  }

  public init = async (): Promise<void> => {
    if (!this.initialized) {
      await this.producer.connect();
      await this.consumer.connect();
    }

    const topics = this.baseYamlConfig.kafka.consumerTopics ?? [];
    if (topics.length > 0 || this.connectorTopic) {
      if (this.connectorTopic) {
        Logger.getInstance().info(
          `Default connector topic: ${this.connectorTopic}`,
        );
      }

      // Do not directly subscribe to the given regex topics to maintain backwards compatibility with KafjaJS.
      // This library does not support flags and requires the topics to be written in a specific way.
      // _source_ with flag i becomes /*^_SOURCE_^*/ and not case independent.
      const regexTopics = await this.getRegexTopics();
      const topicsToSubscribeTo: Array<string> = [
        ...this.getRegularTopics(),
        ...regexTopics,
      ];
      if (this.connectorTopic) {
        topicsToSubscribeTo.push(this.connectorTopic);
      }

      await Promise.all(
        topicsToSubscribeTo.map(async (topic: string) => {
          Logger.getInstance().debug('subscribing to', topic);
          await this.consumer.subscribe({ topic });
        }),
      );

      if (this.baseYamlConfig.kafka.autoCommitThreshold) {
        Logger.getInstance().error(
          'autoCommitThreshold is not supported in bulk listener. Please use autoCommitInterval instead',
        );
      }

      await this.consumer.run({
        partitionsConsumedConcurrently:
          this.baseYamlConfig.kafka.partitionsConsumedConcurrently ?? 1,
        eachMessage: this.consume,
      });
    }

    this.initialized = true;
  };

  private consume: (kafkaMessage: KafkaJS.EachMessagePayload) => Promise<void> =
    async (kafkaMessage: KafkaJS.EachMessagePayload) => {
      const message = JSON.parse(
        kafkaMessage.message?.value?.toString() || '{}',
      );

      if (isXodBaseMessageType(message)) {
        const interval = setInterval(() => {
          kafkaMessage.heartbeat();
        }, 5000);

        try {
          let callbackIdentifier = `${message.tenantIdentifier}_${message.eventType}`;

          if (this.baseYamlConfig.debug)
            Logger.getInstance().debug(
              `Received message from topic ${callbackIdentifier}: ${JSON.stringify(message)}`,
            );

          if (!this.callbackWrappers.has(callbackIdentifier)) {
            callbackIdentifier = RdKafkaBaseService.DEFAULT_CALLBACK_EVENT_TYPE;
          }

          const callbackWrapper = this.callbackWrappers.get(callbackIdentifier);

          if (!callbackWrapper) {
            if (this.baseYamlConfig.debug)
              Logger.getInstance().debug(
                `Callback function for topic ${callbackIdentifier} not set`,
              );

            return;
          }

          // eslint-disable-next-line no-restricted-syntax
          for (const callback of callbackWrapper) {
            // eslint-disable-next-line no-await-in-loop
            const result = await callback.callback(message);
            // eslint-disable-next-line no-await-in-loop
            await this.reportResult(message, result, callback.identifier);
          }
        } catch (error: any) {
          Logger.getInstance().debug(
            `Error processing message: ${error.message}`,
          );
        } finally {
          clearInterval(interval);
        }
      } else {
        Logger.getInstance().debug(
          `Message nr ${kafkaMessage.message.offset} is not a valid XodActionType`,
        );
      }
    };

  private reportResult = async (
    message: XodBaseMessageType,
    result: KafkaCallbackResponse,
    responseSource: string,
  ): Promise<void> => {
    if (this.disableLogs) {
      Logger.getInstance().debug(
        'Skipping report result due to disableLogs flag',
      );
      return;
    }

    const returnMessage = {
      type: message.type,
      message,
      result,
      responseSource,
      timestamp: new Date().getTime(),
    };

    const topic = `${message.tenantIdentifier}${this.processedTopic}`;

    if (this.baseYamlConfig.debug)
      Logger.getInstance().debug(
        `Sending message to logs topic ${topic} ${JSON.stringify(returnMessage)}`,
      );

    await this.producer.send({
      topic,
      messages: [
        {
          value: JSON.stringify(returnMessage),
        },
      ],
    });
  };
}
